import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import { amber, deepOrange, red, blue} from '@material-ui/core/colors';
import CautionIcon from '@material-ui/icons/Warning';
import WarningIcon from '@material-ui/icons/ErrorOutline';
import NoteIcon from '@material-ui/icons/Info';
import clsx from "clsx";

const useStyles = makeStyles((theme) => ({
    margin: {
        margin: theme.spacing(1),
    },
    root:{
        pointerEvents : "auto !important",
    },
    DialogAction:{
        justifyContent: "center"
    },
    NoteMessage:{
        textAlign: "center",
        fontSize: "16px",
        fontWeight: "bold"
    },
    MessagePopup:{
        fontSize: "20px",
        fontWeight: "bold",
        marginTop: "10px",
        marginLeft: 0,
        marginRight: 0,
        marginBottom: 0,
        padding:0,
        color: "black",
        position: "absolute",
        left: "45%"
    },
    DialogTitle:{
        display: "flex",
        flexDirection: "row",
        flexWrap: "nowrap",
        justifyContent: "space-between",
    },
    DialogIcon:{
        width: "30px",
        height: "30px",
        margin: "8px",
        fill: "black",
        position: "relative",
        left: "35%"
    },
    Note:{
        color: theme.palette.getContrastText(blue["900"]),
        backgroundColor: "#0774B9",
    },
    Warning:{
        color: theme.palette.getContrastText("#F1592A"),
        backgroundColor: "#F1592A",
    },
    Danger:{
        color: theme.palette.getContrastText(red["A700"]),
        backgroundColor: red["A700"],
    },
    Caution:{
        color: theme.palette.getContrastText(amber[500]),
        backgroundColor:"#FFFF00",
    }
}));

export default function FormDialog() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [titleText, setTitleText] = React.useState([]);
    const [messageText, setMessageText] = React.useState("");
    const [buttonType, setButtonType] = React.useState([]);

    const resetPopup = () => {
        setTitleText([]);
        setButtonType([]);
    }

    window.setMessagesPopup = function setMessagesPopup(titleTextPopup, messageTextPopup){
        try{
            titleTextPopup = titleTextPopup.replace(/\s/g,'');
        }
        catch(err){
            titleTextPopup = "";
        }
        if(titleTextPopup !== null && titleTextPopup !== "" && titleTextPopup !== undefined) {
            // setOpen(true);
            setTitleText(titleTextPopup);
            setMessageText(messageTextPopup);
            // console.log(titleTextPopup);
            if (titleTextPopup === "NOTE") {
                setTitleText(titleText.concat(
                    <div id="form-dialog-title" className={clsx(classes.Note, classes.DialogTitle)}>
                        <NoteIcon id={"NoteIcon1"} key={"NoteIcon1"} className={classes.DialogIcon}/>
                        <p className={classes.MessagePopup}>{titleTextPopup}</p>
                        {/*<NoteIcon id={"NoteIcon2"} key={"NoteIcon2"} className={classes.DialogIcon}/>*/}
                    </div>
            ));
            } else if (titleTextPopup === "CAUTION") {
                setTitleText(titleText.concat(
                    <div id="form-dialog-title" className={clsx(classes.Caution, classes.DialogTitle)}>
                        <CautionIcon id={"CautionIcon1"} key={"CautionIcon1"} className={classes.DialogIcon}/>
                        <p className={classes.MessagePopup}>{titleTextPopup}</p>
                    </div>
                ));
            } else if (titleTextPopup === "WARNING") {
                setTitleText(titleText.concat(
                    <div id="form-dialog-title" className={clsx(classes.Warning, classes.DialogTitle)}>
                        <CautionIcon id={"WarningIcon1"} key={"WarningIcon1"} className={classes.DialogIcon}/>
                        <p className={classes.MessagePopup}>{titleTextPopup}</p>
                    </div>
                ));
            } else if (titleTextPopup === "DANGER") {
                setTitleText(titleText.concat(
                    <div id="form-dialog-title" className={clsx(classes.Danger, classes.DialogTitle)}>
                        <CautionIcon id={"DangerIcon1"} key={"DangerIcon1"} className={classes.DialogIcon}/>
                        <p className={classes.MessagePopup}>{titleTextPopup}</p>
                    </div>
                ));
            }
        }
        handleClose();
    }

    const handleClose = () => {
        // setTimeout(function () {
            // setOpen(false);
            // resetPopup();
            window.Cortona3DSolo.app.procedure.play();
        // }, 5000);
    };

    const CautionButton = withStyles((theme) => ({
        root: {
            color: theme.palette.getContrastText(amber[500]),
            backgroundColor: amber[500],
            fontSize: "21px",
            '&:hover': {
                backgroundColor: amber[700],
            },
        },
    }))(Button);

    const WarningButton = withStyles((theme) => ({
        root: {
            color: "black",
            backgroundColor: "#F1592A",
            fontSize: "21px",
            '&:hover': {
                backgroundColor: deepOrange[800],
            },
        },
    }))(Button);

    const DangerButton = withStyles((theme) => ({
        root: {
            color: "black",
            backgroundColor: red["A700"],
            fontSize: "21px",
            '&:hover': {
                backgroundColor: red["A700"],
            },
        },
    }))(Button);

    const NoteButton = withStyles((theme) => ({
        root: {
            color: "black",
            backgroundColor: "#0774B9",
            fontSize: "21px",
            '&:hover': {
                backgroundColor: blue["900"],
            },
        },
    }))(Button);

    return (
        <div id={"NoteDiv"}>
            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title" disableBackdropClick={true}
                    disableEscapeKeyDown={true} maxWidth="md" className={classes.root}>
                {/*{titleText}*/}
                {/*<DialogContent>*/}
                {/*    <div className={classes.margin}>*/}
                {/*        <p className={classes.NoteMessage}>*/}
                {/*            {messageText}*/}
                {/*        </p>*/}
                {/*    </div>*/}
                {/*</DialogContent>*/}
                {/*<DialogActions className={classes.DialogAction}>*/}
                {/*    {buttonType}*/}
                {/*</DialogActions>*/}
            </Dialog>
        </div>
    );
}