import React, {useEffect} from 'react';
import MenuIcon from '@material-ui/icons/Menu';
import ListAltIcon from '@material-ui/icons/ListAlt';
import {makeStyles, createTheme, ThemeProvider} from '@material-ui/core/styles';
import clsx from "clsx";
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import View_3D from '../Assets/3d.svg';
import View_Multi from '../Assets/cube.svg';
import Overlay from "react-bootstrap/Overlay";
import View3D from "./View3D";
import ViewMulti from "./ViewMulti";
import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import ResetIcon from '@material-ui/icons/Cached';
import Shapes2DIcon from '@material-ui/icons/InsertPhoto';
import TransparencyIcon from '@material-ui/icons/SettingsBrightness';
import IsolateIcon from '@material-ui/icons/Adjust';
import HelpIcon from '@material-ui/icons/Help';
import PrintIcon from '@material-ui/icons/Print';
import {Popup} from "reactjs-popup";
import Button from "@material-ui/core/Button";
import ViewTransparency from "./ViewTransparency";
// import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
// import Badge from '@material-ui/core/Badge';
// import {CartContext} from "./CartContext";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import HelpPopup from "./HelpPopup";
import Slider from "@material-ui/core/Slider";
// import ViewDivHeight from "./ViewDivHeight";

const useStyles = makeStyles((theme) => ({
    root: {
        height: "100%",
        zIndex: 10,
        left: "5px",
        margin: 0,
        padding: 0,
        display: "flex",
        flexDirection: "column"
    },
    rootSlider: {
        width: 300,
        height: 73,
        border: 0,
        backgroundColor: '#00152C',
        borderRadius: 0,
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    },
    slider: {
        marginLeft: "10px",
        width: "250px",
        color: 'white',
        height: 8,
        "& .MuiSlider-thumb": {
            height: 24,
            width: 24,
            backgroundColor: 'white',
            border: '2px solid currentColor',
            marginTop: -8,
            marginLeft: -12,
        },
        "& .MuiSlider-rail": {
            height: 8,
            borderRadius: 4,
        },
        "& .MuiSlider-track": {
            height: 8,
            borderRadius: 4,
        }
    },
    showTransparent: {
        display: "block",
        position: 'absolute',
        top: '338px',
        left: '74px',
    },
    icon: {
        height: "50px",
        width: "50px",
    },
    toggleButtonImg: {
        border: 0,
        borderRadius: 0,
        backgroundColor: 'white',
        color: "#00152C",
        '& img': {
            transform: "scale(0.8)"
        },
        '&.Mui-selected': {
            backgroundColor: '#00152C',
            color: "white",
            '& img': {
                filter: "brightness(0) invert(1)",
            },
            '&:hover': {
                backgroundColor: '#A4BCCE',
                '& img': {
                    filter: "brightness(0) invert(0)",
                },
            },
        },
        '&:hover': {
            backgroundColor: '#00152C',
            '& img': {
                filter: "brightness(0) invert(1)",
            },
        },
    },
    toggleButton: {
        border: 0,
        borderRadius: 0,
        backgroundColor: 'white',
        // color: "#034E7A",
        color: "#00152C",
        '&.Mui-selected': {
            backgroundColor: '#00152C',
            color: "white",
            '&:hover': {
                color: '#00152C',
                backgroundColor: '#A4BCCE',
            },
        },
        '&:hover': {
            backgroundColor: '#00152C',
            color: 'white'
        },
    },
    showMultiTCTrue: {
        display: "block",
        position: 'absolute',
        top: '71px',
        left: '74px',
    },
    showMultiTCFalse: {
        display: "block",
        position: 'absolute',
        left: '74px',
    },
    show3DTCTrue: {
        display: "block",
        position: 'absolute',
        top: '143px',
        left: '74px',
    },
    show3DTCFalse: {
        display: "block",
        position: 'absolute',
        top: '71px',
        left: '74px',
    },
}));

const contentStyle = {
    background: "white",
    height: "70%",
    width: "70%"
};

const overlayStyle = {background: 'rgba(0,0,0,0.5)'};

const modal = {
    background: "white",
    height: "70%",
    width: "70%"
};

const theme = createTheme({
    direction: "rtl"
});


const MiniDrawer = (props) => {
    const classes = useStyles();

    const [view, setView] = React.useState('');
    const handleChange = (event, nextView) => {
        setView(nextView);
    };

    // const [viewGrp2, setViewGrp2] = React.useState('');
    const [viewGrp2, setViewGrp2] = React.useState('Manual');
    const handleChangeGrp2 = (event, nextView) => {
        setViewGrp2(nextView);
        if (nextView === "Manual") {
            window.setManualDiv(true);
        } else {
            window.setManualDiv(false);
        }
    };


    useEffect(() => {

        //close button when click outside
        document.addEventListener('mousedown', (e) => {
            try {
                var id = window.$(e.target).attr('id');
                var elClass = window.$(e.target).attr('class');
                var elParent = window.$(e.target).parents().attr("class");
                if (!(elClass.includes("_3dViewSvg") || elClass.includes("MultiViewSvg") || elClass.includes("MultiViewBtn") || elClass.includes("_3dViewBtn") || elClass.includes("TransparencySvg") || elClass.includes("TransSlider") || elParent.includes("TransSlider") || id === "_3dViewBtn" || id === "MultiViewBtn" || id === "TransparencyBtn" || id === "transparentSlider" || id === "showTransparent")) {
                    menuOnce('')
                }
            } catch (e) {
                console.log(e)
            }

        });
    }, []);


    const [viewGrp3, setViewGrp3] = React.useState('');
    const handleChangeGrp3 = (event, nextView) => {
        setViewGrp3(nextView);
    };

    //Sheet View
    const [showSheet, setShowSheet] = React.useState(false);
    const targetSheet = React.useRef(null);

    //2d view button
    const [buttonState, setbuttonState] = React.useState(false);


    //3D View
    const [show3D, setShow3D] = React.useState(false);
    const target3D = React.useRef(null);


    function View3Dclick() {
        setShow3D(!show3D)

        // Close other menu
        menuOnce('3D');


        if (window.isTCPublish === false) { // remove
            if (window.$('#show3D').hasClass(classes.show3DTCFalse)) {
                window.$('#show3D').addClass('no-display');
                window.$('#show3D').removeClass(classes.show3DTCFalse);
                window.$('#_3dViewBtn').removeClass('Mui-selected');
            } else {
                window.$('#show3D').addClass(classes.show3DTCFalse);
                window.$('#show3D').removeClass('no-display');
                window.$('#_3dViewBtn').addClass('Mui-selected');
            }
        } else if (window.isTCPublish === true) {
            if (window.$('#show3D').hasClass(classes.show3DTCTrue)) {
                window.$('#show3D').addClass('no-display');
                window.$('#show3D').removeClass(classes.show3DTCTrue);
                window.$('#_3dViewBtn').removeClass('Mui-selected');
            } else {
                window.$('#show3D').addClass(classes.show3DTCTrue)
                window.$('#show3D').removeClass('no-display');
                window.$('#_3dViewBtn').addClass('Mui-selected');

            }
        }
    }

    //Multi View
    const [showMulti, setShowMulti] = React.useState(false);
    const targetMulti = React.useRef(null);

    function multiViewClick() {
        setShowMulti(!showMulti)

        // Close other menu
        menuOnce('Multi');

        if (window.isTCPublish === false) { // remove
            if (window.$('#showMulti').hasClass(classes.showMultiTCFalse)) {
                window.$('#showMulti').addClass('no-display');
                window.$('#showMulti').removeClass(classes.showMultiTCFalse);
                window.$('#MultiViewBtn').removeClass('Mui-selected');
            } else {
                window.$('#showMulti').addClass(classes.showMultiTCFalse);
                window.$('#showMulti').removeClass('no-display');
                window.$('#MultiViewBtn').addClass('Mui-selected');
            }
        } else if (window.isTCPublish === true) {
            if (window.$('#showMulti').hasClass(classes.showMultiTCTrue)) {
                window.$('#showMulti').addClass('no-display');
                window.$('#showMulti').removeClass(classes.showMultiTCTrue);
                window.$('#MultiViewBtn').removeClass('Mui-selected');
            } else {
                window.$('#showMulti').addClass(classes.showMultiTCTrue);
                window.$('#showMulti').removeClass('no-display');
                window.$('#MultiViewBtn').addClass('Mui-selected');
            }
        }
    }

    //Transparency View
    const [showTransparency, setShowTransparency] = React.useState(false);
    const targetTransparency = React.useRef(null);
    const [value, setValue] = React.useState(0);
    const handleTransparent = () => {
        let transparent;
        setShowTransparency(!showTransparency);

        // Close other menu
        menuOnce('Transparent');

        if (window.pickedObject) {
            transparent = window.setTransparencySlider(window.pickedObject.handle, window.pickedObject.name);
            setValue(transparent);
            window.Cortona3DSolo.app.setObjectPropertyf(window.pickedObject.handle, window.Cortona3DSolo.app.PROPERTY_TRANSPARENCY, false, transparent);
        }

        if (window.$('#showTransparent').hasClass('no-display')) {
            window.$('#showTransparent').removeClass('no-display');
            window.$('#showTransparent').addClass(classes.showTransparent);
            window.$('#TransparencyBtn').addClass('Mui-selected');
        } else {
            window.$('#showTransparent').removeClass(classes.showTransparent);
            window.$('#showTransparent').addClass('no-display');
            window.$('#TransparencyBtn').removeClass('Mui-selected');
        }
    };
    const handleTransparentChange = (event, newValue) => {
        try {
            console.log('New Value ' + newValue);
            console.log('Slider Value ' + value);
            if (window.pickedObject.handle) {
                setValue(newValue);
                window.Cortona3DSolo.app.setObjectPropertyf(window.pickedObject.handle, window.Cortona3DSolo.app.PROPERTY_TRANSPARENCY, false, newValue);
            }
        } catch (err) {
            setValue(0);
            console.log(err)
        }
    };

    // Show only one menu
    function menuOnce(tag) {
        window.$('#MultiViewBtn').removeClass('Mui-selected');
        window.$('#_3dViewBtn').removeClass('Mui-selected');
        window.$('#TransparencyBtn').removeClass('Mui-selected');
        if (tag === '3D') {
            window.$('#showMulti').addClass('no-display');
            window.$('#showMulti').removeClass(classes.showMultiTCFalse);
            window.$('#showMulti').removeClass(classes.showMultiTCTrue);
            window.$('#showTransparent').addClass('no-display');
            window.$('#showTransparent').removeClass(classes.showTransparent);
        } else if (tag === 'Multi') {
            window.$('#show3D').addClass('no-display');
            window.$('#show3D').removeClass(classes.show3DTCTrue);
            window.$('#show3D').removeClass(classes.show3DTCFalse);
            window.$('#showTransparent').addClass('no-display');
            window.$('#showTransparent').removeClass(classes.showTransparent);
        } else if (tag === 'Transparent') {
            window.$('#showMulti').addClass('no-display');
            window.$('#showMulti').removeClass(classes.showMultiTCFalse);
            window.$('#showMulti').removeClass(classes.showMultiTCTrue);
            window.$('#show3D').addClass('no-display');
            window.$('#show3D').removeClass(classes.show3DTCTrue);
            window.$('#show3D').removeClass(classes.show3DTCFalse);
        } else {
            window.$('#showMulti').addClass('no-display');
            window.$('#showMulti').removeClass(classes.showMultiTCFalse);
            window.$('#showMulti').removeClass(classes.showMultiTCTrue);
            window.$('#show3D').addClass('no-display');
            window.$('#show3D').removeClass(classes.show3DTCTrue);
            window.$('#show3D').removeClass(classes.show3DTCFalse);
            window.$('#showTransparent').addClass('no-display');
            window.$('#showTransparent').removeClass(classes.showTransparent);
        }
    }

    //ShowDivHeight View
    const [showDivHeight, setShowDivHeight] = React.useState(false);
    const targetDivHeight = React.useRef(null);

    // Show part
    const handleShowPart = () => {
        // Close other menu
        menuOnce('');
        try {
            window.Cortona3DSolo.app.setObjectPropertyf(window.pickedObject.handle, window.Cortona3DSolo.app.PROPERTY_TRANSPARENCY, true, 0);
        } catch (err) {

        }
    }
    // Hide part
    const handleHidePart = () => {
        // Close other menu
        menuOnce('');
        try {
            window.Cortona3DSolo.app.setObjectPropertyf(window.pickedObject.handle, window.Cortona3DSolo.app.PROPERTY_TRANSPARENCY, true, 1);
        } catch (err) {

        }
    }

    // 2D View Popup
    const handle2DView = () => {
        // window.set2DPopup(window.m_sheet_drawing,window.m_sheet_desc);
        window.Cortona3DSolo.app.ipc.toggleDrawingDisplayMode();
    }

    // Cart Popup
    const handleCart = () => {
        window.setCartPopup("Shopping Cart");
    }

    // Reset View
    const handleResetView = () => {
        window.Cortona3DSolo.app.restoreObjectProperty(0, window.Cortona3DSolo.app.PROPERTY_TRANSPARENCY, false);

        var procedure = window.Cortona3DSolo.app.procedure;
        var beginID = window.$('.divStep.stepSelected').attr("id");
        procedure.seekToSubstep(beginID);

        // Close other menu
        menuOnce('');
    }

    // Help View
    const handleHelpView = () => {
        // Close other menu
        menuOnce('');

        window.Cortona3DSolo.app.ipc.resetCurrentSheet(false);
        window.Cortona3DSolo.app.restoreObjectProperty(null, window.Cortona3DSolo.app.PROPERTY_TRANSPARENCY, false);
        window.$("input[type=checkbox]").prop('checked', "checked");
    }

    //BI: Print Output
    const PrintOutputHandler = () => {
        // Close other menu
        menuOnce('');

        let pageNumOri = window.$(".PrintOutput > table.tablePage").find('.Page-Footer').length;
        let totalNewPage = newPageCount('#tablePrint-data');
        addNewPage(totalNewPage, pageNumOri);

        // TC Publish
        if (window.isTCPublish === true) {
            console.log("TC Publish true");
            window.$('#PrintTabPart_include').removeClass('no-display');
            window.$('#PrintTabSupply_include').removeClass('no-display');
            window.$('#PrintTabSST_include').removeClass('no-display');

        }

        // Get animation from screen
        window.$("#solo-canvas").detach().appendTo("#canvas");
        window.$("#tablePrint-data > *").detach().appendTo("#documentTable");

        window.print()

        // put content to ori place in screen
        window.$("#solo-canvas").detach().appendTo("#animation");
        window.$("#documentTable > *").detach().appendTo("#tablePrint-data");
        window.$('.PrintOutput > .pageDynamic').removeClass('PrintOnly');
    };

    function newPageCount(tablePath) {
        let totalNewPage, maxHeight;

        if (window.isTCPublish) {
            maxHeight = 254;       //unit mm.
        } else {
            maxHeight = 232;       //unit mm.
        }

        window.$("#tablePrint-data").removeClass("no-display");    //Remove class to get clientHeight value
        let height = document.querySelector(tablePath).clientHeight * 0.265;
        totalNewPage = Math.floor(height / maxHeight)
        console.log("Document page total " + totalNewPage + "(" + height + "mm [" + maxHeight + "mm])");

        window.$("#tablePrint-data").addClass("no-display");
        return totalNewPage;
    }

    function addNewPage(totalNewPage, pageNumOri) {
        let pageNum;
        for (let i = 1; i <= totalNewPage; i++) {
            pageNum = pageNumOri + i;
            console.log("pageNum " + pageNum);
            window.$('.PrintOutput > table:nth-child(' + pageNum + ')').addClass('PrintOnly');
        }
    }

    // Isolate View
    const handleIsolateView = () => {
        window.Cortona3DSolo.app.alignHorizon(true);
    }

    const [sheet, setSheet] = React.useState([]);

    window.setSheetMenuOverlay = function setSheetMenuOverlay(sheetName, sheetID) {
        console.log(sheetID)
        setSheet(sheet.concat(<MenuItem value={sheetID} className={"ContextMenuItem"}
                                        onClick={() => handleSheetChange(sheetID)}>{sheetName}</MenuItem>));
    }

    // Sheet Change
    const handleSheetChange = (sheetID) => {
        try {
            window.Cortona3DSolo.app.ipc.setCurrentSheet(sheetID, true);
            handleClose();
        } catch (err) {
        }
    }

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
        // if(window.isTCPublish){
        //     window.menuClick();
        // }
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    return (
        <div id="MiniDrawerDiv" className={`MiniDrawerDiv ${clsx(classes.root)}`}>
            <ToggleButtonGroup orientation="vertical" value={view} exclusive={false} onChange={handleChange}
                               className={classes.toggleButtonGroup} bgcolor="white">
                {/*{window.isTCPublish && <ToggleButton bgcolor="white" value="MenuView" className={classes.toggleButton} onClick={handleClick}>*/}
                {/*    <MenuIcon className={classes.icon} />*/}
                {/*</ToggleButton>}*/}
                <Button bgcolor="white" id="MultiViewBtn" value="multiView"
                        className={`MultiViewBtn ${classes.toggleButtonImg}`} ref={targetMulti}
                        onClick={() => multiViewClick()}>
                    <img src={View_Multi} className={`MultiViewSvg ${classes.icon}`} alt={View_Multi}/>
                </Button>
                <Button id="_3dViewBtn" value="3DView" className={`_3dViewBtn ${classes.toggleButtonImg}`}
                        ref={target3D} onClick={() => View3Dclick()}>
                    <img src={View_3D} className={`_3dViewSvg ${classes.icon}`} alt={View_3D}/>
                </Button>
            </ToggleButtonGroup>
            <ToggleButtonGroup className="BtnGrp" orientation="vertical" value={viewGrp2} exclusive={true}
                               onChange={handleChangeGrp2}>
                <ToggleButton value="Manual" aria-label="List" className={`TabBtn ${classes.toggleButton}`}>
                    <ListAltIcon className={`TabSvg ${classes.icon}`}/>
                </ToggleButton>
            </ToggleButtonGroup>

            <ToggleButtonGroup orientation="vertical" value={viewGrp3} exclusive={false} onChange={handleChangeGrp3}
                               className={classes.toggleButtonGroup}>
                <Button value="PartShow" className={`ShowBtn ${classes.toggleButton}`} onClick={handleShowPart}>
                    <VisibilityIcon className={`ShowSvg ${classes.icon}`}/>
                </Button>
                <Button value="PartHide" className={`HideBtn ${classes.toggleButton}`} onClick={handleHidePart}>
                    <VisibilityOffIcon className={`HideSvg ${classes.icon}`}/>
                </Button>
                <Button id="TransparencyBtn" value="Transparency" className={`TransparencyBtn ${classes.toggleButton}`}
                        ref={targetTransparency} onClick={handleTransparent}>
                    <TransparencyIcon className={`TransparencySvg ${classes.icon}`}/>
                </Button>
                {/*<ToggleButton value="View2D" className={classes.toggleButton} ref={targetDivHeight} onClick={handle2DView} disabled={buttonState}>*/}
                <ToggleButton id="View2DBtn" value="View2D" className={`View2DBtn ${classes.toggleButton}`}
                              ref={targetDivHeight} disabled={buttonState}>
                    <Shapes2DIcon className={`View2DSvg ${classes.icon}`}/>
                </ToggleButton>
                <Button value="ViewReset" className={`ViewResetBtn ${classes.toggleButton}`} onClick={handleResetView}>
                    <ResetIcon className={`ViewResetSvg ${classes.icon}`}/>
                </Button>
                {/*<Button value="Isolate" className={classes.toggleButton} onClick={handleIsolateView}>*/}
                {/*    <IsolateIcon className={classes.icon} />*/}
                {/*</Button>*/}
                <Button value="Help" className={`HelpBtn ${classes.toggleButton}`} onClick={handleHelpView}>
                    <Popup
                        trigger={<HelpIcon className={`HelpSvg ${classes.icon}`}/>}
                        {...{modal, contentStyle, overlayStyle}} >
                        <HelpPopup/>
                    </Popup>
                </Button>
                //BI 10/5/2022 : PrintOutput button enable/disable depend on existence of 2d image in the current step
                <Button id="PrintOutputBtn" className={`PrintBtn ${classes.toggleButton}`} onClick={PrintOutputHandler}>
                    <PrintIcon className={`PrintSvg ${classes.icon}`}/>
                </Button>
            </ToggleButtonGroup>

            {/*Create Overlay for 3D*/}
            {/*<Overlay target={target3D.current} show={show3D} placement="right">
                <div>
                    <View3D />
                </div>
            </Overlay>*/}
            <div id="show3D" className='no-display'>
                <View3D/>
            </div>

            {/*Create Overlay for Multi*/}
            {/*<Overlay target={targetMulti.current} show={showMulti} placement="right">
                <div>
                    <ViewMulti />
                </div>
            </Overlay>*/}
            <div id="showMulti" className='no-display'>
                <ViewMulti/>
            </div>

            {/*Create Overlay for showTransparency*/}
            {/* <Overlay target={targetTransparency.current} show={showTransparency} placement="right">
                <div>
                    <ViewTransparency />
                </div>
            </Overlay> */}
            <div id="showTransparent" className='no-display'>
                {window.docLanguage === "ar-SA" ? (<ThemeProvider theme={theme}>
                    <div className={`TransSlider ${classes.rootSlider}`}>

                        <Slider id='transparentSlider' className={`TransSlider ${classes.slider}`} value={value}
                                step={0.05} min={0}
                                max={1}
                                onChange={handleTransparentChange}/>

                    </div>
                </ThemeProvider>) : (<div className={`TransSlider ${classes.rootSlider}`}>

                    <Slider id='transparentSlider' className={`TransSlider ${classes.slider}`} value={value} step={0.05}
                            min={0}
                            max={1}
                            onChange={handleTransparentChange}/>

                </div>)}
            </div>

            {/*Create Overlay for showDivHeight*/}
            {/*<Overlay target={targetDivHeight.current} show={showDivHeight} placement="right">*/}
            {/*    <div>*/}
            {/*        <ViewDivHeight />*/}
            {/*    </div>*/}
            {/*</Overlay>*/}

        </div>
    );
};

export default MiniDrawer;
