import React from 'react';
import { makeStyles, useTheme, Theme, createStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import CloseIcon from '@material-ui/icons/Close';
import {Box} from "@material-ui/core";
import clsx from "clsx";

const useStyles = makeStyles((theme) =>
    createStyles({
        root: {
            display: 'flex',
        },
        drawer: {
            width: '68.5%',
            flexShrink: 0,
        },
        drawerPaper: {
            width: '60%',
            display: "inline-table",
            left: '0 !important',
            right: 'auto !important'
        },
        drawerHeader: {
            display: 'flex',
            alignItems: 'center',
            padding: theme.spacing(0, 1),
            // necessary for content to be below app bar
            ...theme.mixins.toolbar,
            justifyContent: 'flex-start',
        },
        closeButton: {
            position: 'absolute',
            right: theme.spacing(1),
            top: theme.spacing(1),
            color: 'white',
        },
    }),
);

export default function Drawer2D() {
    const classes = useStyles();
    const theme = useTheme();
    const [open, setOpen] = React.useState(false);
    const [srcDesc, setSrcDesc] = React.useState("");

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    window.show2DImage = function show2DImage(e) {
        setOpen(true);
        console.log(e)
        setSrcDesc(e.getAttribute("data-src"));
    }

    window.show2DImage_Xref = function show2DImage_Xref(nodeDocID) {
        setOpen(true);
        console.log(nodeDocID)
        //Use jquery to determine src
        if(window.$(".divPart[data-docid='" + nodeDocID + "'] > img").length){
            var imgSrc = window.$(".divPart[data-docid='" + nodeDocID + "'] > img").attr("src")
            setSrcDesc(imgSrc);
        }

    }

    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (
        <div className={classes.root}>
            <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="left"
                open={open}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={clsx("drawer_header", classes.drawerHeader)}>
                    <Typography variant="h6">{srcDesc}</Typography>
                    <IconButton onClick={handleDrawerClose} className={classes.closeButton}>
                        {theme.direction === 'rtl' ? <ChevronLeftIcon /> : <CloseIcon />}
                    </IconButton>
                </div>
                <Box height={"0.9"}>
                    <div id="holder-2D">
                        <img src={srcDesc} className="box_image"/>
                    </div>
                </Box>

            </Drawer>
        </div>
    );
}
