import React from 'react';
import Grid from "@material-ui/core/Grid";
import Slider from '@material-ui/core/Slider';
import PlayArrow from '@material-ui/icons/PlayArrow';
import Pause from '@material-ui/icons/Pause';
import StopIcon from '@material-ui/icons/Stop';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import {makeStyles, withStyles, createTheme, ThemeProvider} from '@material-ui/core/styles';
import clsx from "clsx";
// import ToggleButton from '@material-ui/lab/ToggleButton';
// import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
// import Divider from '@material-ui/core/Divider';
// import ExitToAppIcon from '@material-ui/icons/ExitToApp';
// import GetAppIcon from '@material-ui/icons/GetApp';

const useStyles = makeStyles((theme) => ({
    root: {
        // width: "50%",
        // right: "41%",
        width: "70%",
        right: "15%",
        left: "15%",
        // zIndex: 10,
        position: "relative",
        flexGrow: 1
    },
    icon: {
        height: "35px",
        width: "35px",
        marginRight: "5px",
        verticalAlign: "middle",
        '&:hover': {
            color: '#FFCD31'
        },
        cursor: "pointer"
    },
    gridItem: {
        backgroundColor: 'white',
        margin: 0,
    },
    disableGridItem: {
        pointerEvents: "none",
        opacity: 0.3
    },
    modeButton: {
        textTransform: "none",
        fontSize: 14,
        fontWeight: "bold",
        border: 0,
        borderRadius: 5,
        backgroundColor: 'white',
        color: "black",
        '&.Mui-selected': {
            borderRadius: 5,
            backgroundColor: '#f1f1f1',
            color: "black",
            '&:hover': {
                color: 'black',
                backgroundColor: '#f1f1f1',
            },
        },
    },
    dividerLine: {
        width: 2,
        backgroundColor: "black",
        height: 30,
        margin: "10px !important"
    },
}));

const PrettoSlider = withStyles({
    root: {
        color: '#000000',
        height: 8,
        marginTop: "3px"
    },
    thumb: {
        height: 20,
        width: 20,
        backgroundColor: '#000000',
        border: '2px solid currentColor',
        marginTop: -5,
        marginLeft: -12,
        '&:focus, &:hover, &$active': {
            boxShadow: 'inherit',
        },
    },
    active: {},
    valueLabel: {
        left: 'calc(-50% + 4px)',
    },
    track: {
        height: 8,
        marginTop: 2
    },
    rail: {
        height: 8,
        marginTop: 2
    },
})(Slider);

const Mediabar = (props) => {
    const classes = useStyles();

    const [value, setValue] = React.useState(0);
    const [minValue, setMinValue] = React.useState(0);
    const [maxValue, setMaxValue] = React.useState(0);
    const [stepValue, setStepValue] = React.useState(0);
    const [playDisable, setPlayDisable] = React.useState(false);
    const [pauseDisable, setPauseDisable] = React.useState(true);
    const [signoffDisable, setSignoffDisable] = React.useState(true);
    const [backDisable, setBackDisable] = React.useState(false);
    const [nextDisable, setNextDisable] = React.useState(false);

    // open dev tools and call triggerChange('your new value')
    window.triggerChangeMediabar = function triggerChangeMediabar(id, nextValue) {
        if (id === "btn_play") {
            setPlayDisable(nextValue);
        } else if (id === "btn_pause") {
            setPauseDisable(nextValue);
        }
    }
    window.triggerChangeSignOff = function triggerChangeSignOff(nextValue) {
        setSignoffDisable(nextValue);
    }
    window.triggerChangeBackNext = function triggerChangeBackNext(nextValue) {
        setBackDisable(nextValue);
        setNextDisable(nextValue);
    }

    const [mode, setMode] = React.useState('preview');
    const handleModeChange = (event, nextMode) => {
        setMode(nextMode);
    };

    const handleChange = (event, newValue) => {
        setValue(newValue);
        window.Cortona3DSolo.app.procedure.setPlayPosition(newValue, true);
    };


    window.setSlider = function setSlider(min, max, value) {
        var step = (max - min) / 1000;
        setMinValue(min);
        setMaxValue(max);
        setStepValue(step);
        setValue(value);
    }

    window.setSliderValue = function setSliderValue(value) {
        setValue(value);
    }

    const handlePlay = () => {
        window.Cortona3DSolo.app.procedure.play();
        // window.Cortona3DSolo.app.removeHighlightObjects();
        // while (window.clickedPart.length > 0) {
        //     window.clickedPart.pop();
        // }
        // window.removeAllHighlightedParts();
    }

    const handlePause = () => {
        window.Cortona3DSolo.app.procedure.pause();
    }

    const handleBack = () => {
        var procedure = window.Cortona3DSolo.app.procedure;
        if (!window.m_locked) {
            // if (window.g_simulation_filtered_action_Ids[0] ==="") {
            //     procedure.stop();
            //     procedure.setPlayRange();
            //     procedure.backward();
            //     window.setPlayRangeToCurrentStep();
            // } else {
            var currentID = window.$('.divStep.stepSelected').attr("id");
            var prevID = window.$("div[id=" + currentID + "]").prevAll().not(".no-display").attr("id");
            console.log("Current ID : " + currentID)
            console.log("Previous ID : " + prevID)
            procedure.seekToSubstep(prevID);
            // }
        } else {
            procedure.backward();
        }
    }

    const handleNext = () => {
        var procedure = window.Cortona3DSolo.app.procedure;
        if (!window.m_locked) {
            // if (window.g_simulation_filtered_action_Ids[0] ==="") {
            //     procedure.stop();
            //     procedure.setPlayRange();
            //     procedure.forward();
            //     window.setPlayRangeToCurrentStep();
            // } else {
            var currentID = window.$('.divStep.stepSelected').attr("id");
            var nextID = window.$("div[id=" + currentID + "]").nextAll().not(".no-display").attr("id");
            console.log("Current ID : " + currentID)
            console.log("Next ID : " + nextID)
            procedure.seekToSubstep(nextID);
            // }

        } else {
            procedure.forward();
        }
    }

    function valueLabelFormat(value) {
        return Math.round(value);
    }

    const handlePreviewMode = () => {
        window.$("html").removeClass("job");
        window.$("html").addClass("preview");
        window.currentMode = window.modeArray[0];
        if (window.g_simulation_filtered_action_Ids[0] ==="") {
            window.setPlayRangeToProcedure();
        } else {
            window.setPlayRangeToProcedure_Filter();
        }

        window.triggerChangeSignOff(true);
        window.triggerChangeBackNext(false);
    }

    const handleSignOff = () => {
        //alert("Sign Off clicked");
        window.signOffButtonClicked = true;
        if (window.$('.divStep.stepSelected').find("p.signOffText").length !==0) {
            window.$('.divStep.stepSelected p.signOffText').remove();
        }
        var signOffText = window.getSignOffTime();
        var substepNumber = window.$('.divStep.stepSelected  > div.tiramisu-proc-item-number').text();
        window.$('.divStep.stepSelected').prepend("<p class='signOffText' data-substep='" + substepNumber + "' data-date='" + signOffText[1] + "' data-time='" + signOffText[2] + "'>" + signOffText[0] + "</p>");
        var procedure = window.Cortona3DSolo.app.procedure;
        if (!window.m_locked) {
            procedure.stop();
            var nextID = "";
            var set = window.$('.divStep').not(".no-display");
            var setLength = set.length;
            set.each(function (index) {
                var pso = window.$(this).find("p.signOffText");
                if (pso.length < 1) {
                    nextID = window.$(this).attr("id");
                    return false;
                }
                if (index === (setLength - 1)) {
                    nextID = set[index].id;
                }
            });
            console.log(nextID)
            if (window.g_simulation_filtered_action_Ids[0] ==="") {
                procedure.setPlayRange(nextID);
                procedure.seekToSubstep(nextID);
                window.setPlayRangeToCurrentStep();
            } else {
                var firstID = window.$("div[id=" + nextID + "] > div.tiramisu-proc-item-content > div.substeps > div.tiramisu-proc-item:not(.no-display)").last().attr('id');
                window.setPlayRangeToCurrentStep_Filter(nextID, firstID);
            }
            //handleNext();
        } else {
            procedure.forward();
        }
    }

    const handleJobMode = () => {
        window.$("html").removeClass("preview");
        window.$("html").addClass("job");
        window.currentMode = window.modeArray[1];
        handleStop();
        if (window.g_simulation_filtered_action_Ids[0] ==="") {
            if (window.finishedStep !==null && window.finishedStep !==window.procedureStepsArray[window.procedureStepsArray.length - 1]) {
                var temp1 = window.procedureStepsArray.indexOf(window.finishedStep) + 1;
                window.soloInteractivity.seekToSubstep(window.procedureStepsArray[temp1]);
            }
            window.setPlayRangeToCurrentStep();
        } else {
            var firstID = window.Cortona3DSolo.app.procedure.interactivity.getProcedureItemInfo(window.g_simulation_filtered_action_Ids[0]).parentStep;
            var nextID = window.$("div[id=" + firstID + "] > div.tiramisu-proc-item-content > div.substeps > div.tiramisu-proc-item:not(.no-display)").last().attr('id');
            window.setPlayRangeToCurrentStep_Filter(firstID, nextID);
        }
        handlePlay();
        window.triggerChangeSignOff(false);
        window.triggerChangeBackNext(true);
    }

    const handleStop = () => {
        window.Cortona3DSolo.app.procedure.stop();
    }

    const handleCSVDownload = () => {
        window.generateSignOffFile();
    }

    const theme = createTheme({
        direction: "rtl"
    });

    const mediabarSVGClass = clsx({"MediaBarSvg" : true, [classes.icon] : true});

    return (
        <div className={clsx(classes.root)}>
            <Grid container spacing={2} className={classes.gridItem} justify={"center"} alignItems={"center"}>
                <Grid item className={clsx({[classes.disableGridItem]: playDisable})}>

                    <PlayArrow className={mediabarSVGClass} id={"button-playback"}
                               onClick={handlePlay} style={window.docLanguage === "ar-SA"? {transform: "rotate(180deg)"} : {}}/>

                </Grid>
                <Grid item className={clsx({[classes.disableGridItem]: pauseDisable})}>
                    <Pause className={mediabarSVGClass} id={"button-pause"} onClick={handlePause}/>
                </Grid>
                <Grid item>
                    <StopIcon className={mediabarSVGClass} id={"button-stop"} onClick={handleStop}/>
                </Grid>
                <Grid item className={clsx({[classes.disableGridItem]: backDisable})}>
                    <FirstPage className={mediabarSVGClass} id={"button-back"} onClick={handleBack} style={window.docLanguage === "ar-SA"? {transform: "rotate(180deg)"} : {}}/>
                </Grid>
                <Grid item className={clsx({[classes.disableGridItem]: nextDisable})}>
                    <LastPage className={mediabarSVGClass} id={"button-next"} onClick={handleNext} style={window.docLanguage === "ar-SA"? {transform: "rotate(180deg)"} : {}}/>
                </Grid>
                <Grid item xs>
                    {window.docLanguage === "ar-SA" ? (
                        <ThemeProvider theme={theme}>
                            <PrettoSlider valueLabelDisplay="auto" aria-label="pretto slider"
                                          defaultValue={0} value={value} onChange={handleChange}
                                          min={minValue} max={maxValue} step={stepValue}
                                          valueLabelFormat={valueLabelFormat}/>
                        </ThemeProvider>) : (<PrettoSlider valueLabelDisplay="auto" aria-label="pretto slider"
                                                           defaultValue={0} value={value} onChange={handleChange}
                                                           min={minValue} max={maxValue} step={stepValue}
                                                           valueLabelFormat={valueLabelFormat}/>)}
                </Grid>
                {/*<ToggleButtonGroup value={mode} exclusive={true} onChange={handleModeChange}*/}
                {/*                   className={classes.toggleButtonGroup}>*/}
                {/*    <ToggleButton value="preview" id={"preview"} className={classes.modeButton}*/}
                {/*                  onClick={handlePreviewMode}>*/}
                {/*        Preview Mode*/}
                {/*    </ToggleButton>*/}
                {/*    <Divider orientation="vertical" className={classes.dividerLine} flexItem/>*/}
                {/*    <ToggleButton value="job" id={"job"} className={classes.modeButton} onClick={handleJobMode}>*/}
                {/*        Job Mode*/}
                {/*    </ToggleButton>*/}
                {/*</ToggleButtonGroup>*/}
                {/*<Grid item className={clsx({[classes.disableGridItem]: signoffDisable})}>*/}
                {/*    <ExitToAppIcon className={classes.icon} id={"button-signoff"} onClick={handleSignOff}*/}
                {/*                   alignItems={"center"}/>*/}
                {/*    <GetAppIcon className={classes.icon} id={"button-csv"} onClick={handleCSVDownload}*/}
                {/*                alignItems={"center"}/>*/}
                {/*</Grid>*/}
            </Grid>
        </div>
    );
};

export default Mediabar;