import React from "react";
import {makeStyles} from "@material-ui/core/styles";

import Logo from "../Assets/INEOS-logo.png";

const useStyles = makeStyles((theme) => ({
    [`@media screen`]: {
        print: {
            display: 'none',
        },
    },
    [`@media print`]: {
        print: {
            display: 'block',
            counterReset: 'Page',
        },
    },
}));

const PrintOutput = () => {
    // const classes = useStyles();
    const ConvertToArabicNumbers = (num) => {
        const arabicNumbers = '\u0660\u0661\u0662\u0663\u0664\u0665\u0666\u0667\u0668\u0669';
        return new String(num).replace(/[0123456789]/g, (d)=>{return arabicNumbers[d]});
    }

    let nowDate;
    let get_Date = new Date();
    let get_Month = new Date();
    get_Month = get_Month.getMonth() +1
    if(window.docLanguage === "ar-SA"){
        let year = ConvertToArabicNumbers(get_Date.getFullYear());
        nowDate = `${get_Date.getDate().toLocaleString("ar-SA")} / ${get_Month.toLocaleString("ar-SA")} / ${year}`
    } else if(window.docLanguage === 'de-DE') {
        nowDate = `${get_Date.getDate().toLocaleString("de-DE")} / ${get_Month.toLocaleString("de-DE")} / ${get_Date.getFullYear()}`
    } else if(window.docLanguage === 'it-IT') {
        nowDate = `${get_Date.getDate().toLocaleString("it-IT")} / ${get_Month.toLocaleString("it-IT")} / ${get_Date.getFullYear()}`
    } else if(window.docLanguage === 'fr-FR') {
        nowDate = `${get_Date.getDate().toLocaleString("fr-FR")} / ${get_Month.toLocaleString("fr-FR")} / ${get_Date.getFullYear()}`
    } else if(window.docLanguage === 'es-ES') {
        nowDate = `${get_Date.getDate().toLocaleString("es-ES")} / ${get_Month.toLocaleString("es-ES")} / ${get_Date.getFullYear()}`
    } else {
        nowDate = `${get_Date.getDate().toLocaleString("en-US")} / ${get_Month.toLocaleString("en-US")} / ${get_Date.getFullYear()}`
    }

    let pageNumber = 2;
    const pageDynamic = () => {
        pageNumber = pageNumber +1;
        return (
            <table className="pageDynamic">
                <thead>
                    <div className="Page-Header">
                        <div className="Page-Logo"><img src={Logo} className="HeaderLogo" /></div>
                        {headerDetail()}
                    </div>
                </thead>
                <tbody>
                    <div className="PO-Content"></div>
                </tbody>
                <tfoot>
                    <div className="Page-Footer">
                        {dateContent()}
                        {pageNum(pageNumber)}
                    </div>
                </tfoot>
            </table>
        )
    }

    const pageNum = (num) => {
        let number = 0;
        if(window.docLanguage === "ar-SA") {
            return (
                <div className={"Page"} style={{ fontFamily: 'Arial' }}>
                     {ConvertToArabicNumbers(num)}   
                </div>
            );
        } else if(window.docLanguage === 'de') {
            number = 'Seite ' +num;
        } else if(window.docLanguage === 'it') {
            number = 'Pagina ' +num;
        } else if(window.docLanguage === 'fr') {
            number = 'Page ' +num;
        } else if(window.docLanguage === 'es') {
            number = 'Pao ' +num;
        } else {
            number = 'Page ' +num;
        }
        return (
            <div className={"Page"} style={{ fontFamily: 'Open Sans Regular' }}>
                {number}
            </div>
        );
    }

    const headerDetail = () => {
        if(window.docLanguage === "ar-SA") {
            return (
                <div className="Page-Detail" style={{ borderRight: '1px solid black'}}>
                    <div className="Header-Row1" style={{ fontFamily: 'Arial' }} />
                    <div className="Header-Row2" style={{ fontFamily: 'Arial' }} />
                    <div className="Header-Row3" style={{ fontFamily: 'Arial' }} />
                </div>
            );
        } else {
            return (
                <div className="Page-Detail" style={{ borderLeft: '1px solid black'}}>
                    <div className="Header-Row1" style={{ fontFamily: 'Open Sans Regular' }} />
                    <div className="Header-Row2" style={{ fontFamily: 'Open Sans Regular' }} />
                    <div className="Header-Row3" style={{ fontFamily: 'Open Sans Regular' }} />
                </div>
            );
        }
    }

    const dateContent = () => {
        if(window.docLanguage === "ar-SA") {
            return (
                <>
                    <div className="Date" style={{ fontFamily: 'Arial' }}>{nowDate}</div>
                </>
            );
        } else {
            return (
                <>
                    <div className="Date" style={{ fontFamily: 'Open Sans Regular' }}>{nowDate}</div>
                </>
            );
        }
    }

    return (
        <div className="PrintOutput">
            <table className="tablePage">
                <thead>
                    <div className="Page-Header">
                        <div className="Page-Logo"><img src={Logo} className="HeaderLogo" /></div>
                        {headerDetail()}
                    </div>
                </thead>
                <tbody>
                    <div id="canvas"></div>
                </tbody>
                <tfoot>
                    <div className="Page-Footer">
                        {dateContent()}
                        {pageNum(1)}
                    </div>
                </tfoot>
            </table>
            <table className="tablePage">
                <thead>
                    <div className="Page-Header">
                        <div className="Page-Logo"><img src={Logo} className="HeaderLogo" /></div>
                        {headerDetail()}
                    </div>
                </thead>
                <tbody>
                    <div id="documentTable" />
                </tbody>
                <tfoot>
                    <div className="Page-Footer">
                        {dateContent()}
                        {pageNum(2)}
                    </div>
                </tfoot>
            </table>

            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
        </div>
    );
};

export default PrintOutput;
