import React, {useEffect} from 'react';
import PropTypes from 'prop-types';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import {withStyles} from "@material-ui/core";
import clsx from "clsx";
import CautionIcon from "@material-ui/icons/Warning";


const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: "#bad0db",
        // height: "100%",
        width: "100%"
    }, tab: {
        backgroundColor: "#bad0db",
        height: "100%",
        width: "100%"

    },
    Title: {
        color: 'white',
        fontWeight: "bold"
    },
    indtab: {
        backgroundColor: "#bad0db",
        borderTop: "none",
        borderBottom: "solid 5px white",
        color: "black",
        fontSize: "14px",
        textTransform: "capitalize"
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
}));

const GlobalCss = withStyles({
    '@global': {
        '.Mui-selected': {
            backgroundColor: "#00152C",
            color: "#FFFFFF",
            fontWeight: "bold",
        },
    },
})(() => null);

function TabPanel(props) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Container>
                    <Box>
                        {children}
                    </Box>
                </Container>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};


function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

export default function Manual() {
    const theme = useTheme();
    const [value, setValue] = React.useState(1);
    const [showAllState, setShowAllState] = React.useState(true);
    const [showTorqueState, setShowTorqueState] = React.useState(true);
    const [detailTable, setDetailTable] = React.useState([]);
    const [detailTab, setDetailTab] = React.useState([]);
    const [contentHeight, setContentHeight] = React.useState([]);


    const changeHeight = () =>{
        const bodyHeight = window.$('body').height();
        const tabHeaderHeight = window.$('.tab-header').height();
        console.log("html height: " + bodyHeight);
        console.log("tab header: " + tabHeaderHeight);

        const tabContentHeight = bodyHeight;
        console.log("tab content: " + tabContentHeight);
        window.$(".tab-content").css('height:', tabContentHeight + '!important');

    }



    const tdRowClass = clsx({"dpl-td-vertical" : true,  "td-data": true, "rtl" : window.docLanguage === "ar-SA", "ltr" : window.docLanguage !== "ar-SA"});
    const thRowClass = clsx({"dpl-th-vertical" : true, "rtl" : window.docLanguage === "ar-SA", "ltr" : window.docLanguage !== "ar-SA"});
    const directionClass = window.docLanguage === "ar-SA" ? "rtl" : "ltr";

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    function ShowAll() {
        setShowAllState(true);
    }

    function ShowTorque() {
        setShowAllState(false);
    }

    const handleTabContentClick = (event, newValue) => {
        if (event.target.id) {
            console.log(event.target.id)
            window.$('.selected-row').removeClass('selected-row')
        }
    };

    window.setTabView = function setTabView(indexVal) {
        setValue(indexVal);
    }

    window.setDetails = function handleDetails() {
        var hourVariable;
        if (window.repairTime >= 2) {
            hourVariable = window.hoursText;
        } else {
            hourVariable = window.hourText;
        }

        var rTimes;
        if (window.repairTime !== 0.0) {
            rTimes =  window.repairTime;
        } else {
            rTimes = '0.0';
        }
        setDetailTab(
            <TableContainer style={{backgroundColor: 'inherit', boxShadow: 'none', marginTop: '2%'}}
                            component={Paper} id="TabDetails">
                <Table style={{width: '96%', marginLeft: '2%' , marginRight: '2%'}} className={classes.table} size="small"
                       aria-label="simple table" id="details-table-row">
                    <TableBody>
                        {window.titleData &&
                        <TableRow className="description-row">
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.titleHeader : 'Title'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass} scope="row" align="center">
                                {window.titleData}
                            </TableCell>
                        </TableRow>}
                        {window.sectionData &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.sectionHeader : 'Section'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass} scope="row"
                                       align="center">{window.sectionData}
                            </TableCell>
                        </TableRow>}
                        {window.docNoData &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.docNoHeader : 'Doc No.'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.docNoData}</TableCell>
                        </TableRow>}
                        {(window.partNoData && window.partNoData !== 'XXX-XXXX-XXXXXX') &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.partNoHeader : 'Part No.'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.partNoData}</TableCell>
                        </TableRow>}
                        {window.operationData &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th"
                                       align="center">{window.isTCPublish ? window.operationHeader : 'Operation'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.operationData}</TableCell>
                        </TableRow>}
                        {(window.repairTime && window.isTCPublish || window.repairTime === 0.0) &&
                        <TableRow> {/*repair time not yet add metadata*/}
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.repairTimeHeader : 'Repair Time'}</TableCell>
                            <TableCell colSpan={2} className={`repair-time ${tdRowClass}`}
                                       align="center"><b>{`${rTimes} ${hourVariable}`}</b></TableCell>
                        </TableRow>}
                        {window.modelData &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.modelHeader : 'Model'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.modelData}</TableCell>
                        </TableRow>}
                        {window.modelYearData &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.modelYearHeader : 'Model Year'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.modelYearData}</TableCell>
                        </TableRow>}
                        {(window.engineData && window.engineData !== 'Common') &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.engineHeader : 'Engine'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.engineData}</TableCell>
                        </TableRow>}
                        {(window.transmissionData && window.transmissionData !== 'Common') &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th"
                                       align="center">{window.isTCPublish ? window.transmissionHeader : 'Transmission'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.transmissionData}</TableCell>
                        </TableRow>}
                        {(window.lhdRhdData && window.lhdRhdData !== 'Common') &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.lhdHeader : 'LHD / RHD'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.lhdRhdData}</TableCell>
                        </TableRow>}
                        {(window.bodyData && window.bodyData !== 'Common') &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.bodyHeader : 'Body'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.bodyData}</TableCell>
                        </TableRow>}
                        {(window.regionData && window.regionData !== 'Common') &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.regionHeader : 'Region'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.regionData}</TableCell>
                        </TableRow>}
                        {(window.vinFromData && window.vinFromData !== 'XXXXXXXXXXXXXXXXX') &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.vinFromHeader : 'VIN From'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.vinFromData}</TableCell>
                        </TableRow>}
                        {(window.vinToData && window.vinToData !== 'XXXXXXXXXXXXXXXXX') &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.vinToHeader : 'VIN To'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{window.vinToData}</TableCell>
                        </TableRow>}
                        {window.createdDateData &&
                        <TableRow>
                            <TableCell className={`dpl-th-vertical revision-header ${directionClass}`} component="th"
                                       align="center">{window.isTCPublish ? window.revisionsHeader : 'Revisions'}</TableCell>
                            <TableCell className={`dpl-td-vertical revision-row-title ${directionClass}`}
                                       align="center">{window.isTCPublish ? window.createdHeader : 'Created'}</TableCell>
                            <TableCell className={`revision-data ${tdRowClass}`}
                                       align="center">{window.createdDateData}</TableCell>
                        </TableRow>}
                        {window.revisionDateData &&
                        <TableRow>
                            <TableCell className="hide-header" style={{border: 'none'}} align="center"></TableCell>
                            <TableCell className={`dpl-td-vertical revision-row-title ${directionClass}`}
                                       align="center">{window.isTCPublish ? window.revisedHeader : 'Revised'}</TableCell>
                            <TableCell className={`revision-data ${tdRowClass}`}
                                       align="center">{window.revisionDateData}</TableCell>
                        </TableRow>}
                        {window.revisionData &&
                        <TableRow>
                            <TableCell className="hide-header" style={{border: 'none'}} align="center"></TableCell>
                            <TableCell className={`dpl-td-vertical revision-row-title ${directionClass}`}
                                       align="center">{window.isTCPublish ? window.commentsHeader : 'Comments'}</TableCell>
                            <TableCell className={`revision-data ${tdRowClass}`}
                                       align="center">{window.revisionData}</TableCell>
                        </TableRow>}
                    </TableBody>
                </Table>
            </TableContainer>
        );
    }

    window.setIncludeDetails = function handleIncludeDetails(var_Title, var_sectionData, var_docNoData, var_partNoData, var_operationData, var_repairTime, var_modelData, var_modelYearData, var_engineData, var_transmissionData, var_lhdRhdData, var_bodyData, var_regionData, var_vinFromData, var_vinToData, var_revisionDateData, var_revision, var_revisionCreated) {
        var hourVariable;
        if (var_repairTime >= 2) {
            hourVariable = window.hoursText;
        } else {
            hourVariable = window.hourText;
        }

        setDetailTable(detailTable.concat(
            <TableContainer style={{backgroundColor: 'inherit', boxShadow: 'none', marginTop: '2%'}}
                            component={Paper} className="TabIncludesTable">
                <Table style={{width: '96%', marginLeft: '2%', marginRight: '2%'}} className={classes.table} size="small"
                       aria-label="simple table">
                    <TableBody>
                        {var_Title &&
                        <TableRow className="description-row">
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.titleHeader : 'Title'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass} scope="row" align="center">
                                {var_Title}
                            </TableCell>
                        </TableRow>}
                        {var_sectionData &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.sectionHeader : 'Section'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass} scope="row"
                                       align="center">{var_sectionData}
                            </TableCell>
                        </TableRow>}
                        {var_docNoData && <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.docNoHeader : 'Doc No.'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_docNoData}</TableCell>
                        </TableRow>}
                        {var_partNoData !== 'XXX-XXXX-XXXXXX' &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.partNoHeader : 'Part No.'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_partNoData}</TableCell>
                        </TableRow>}
                        {var_operationData &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th"
                                       align="center">{window.isTCPublish ? window.operationHeader : 'Operation'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_operationData}</TableCell>
                        </TableRow>}
                        {var_repairTime &&
                        <TableRow> {/*repair time not yet add metadata*/}
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.repairTimeHeader : 'Repair Time'}</TableCell>
                            <TableCell colSpan={2} className={`repair-time ${tdRowClass}`}
                                       align="center"><b>{`${var_repairTime} ${hourVariable}`}</b></TableCell>
                        </TableRow>}
                        {var_modelData &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.modelHeader : 'Model'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_modelData}</TableCell>
                        </TableRow>}
                        {var_modelYearData &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.modelYearHeader : 'Model Year'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_modelYearData}</TableCell>
                        </TableRow>}
                        {var_engineData !== 'Common' &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.engineHeader : 'Engine'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_engineData}</TableCell>
                        </TableRow>}
                        {var_transmissionData !== 'Common' &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th"
                                       align="center">{window.isTCPublish ? window.transmissionHeader : 'Transmission'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_transmissionData}</TableCell>
                        </TableRow>}
                        {var_lhdRhdData !== 'Common' &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.lhdHeader : 'LHD / RHD'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_lhdRhdData}</TableCell>
                        </TableRow>}
                        {var_bodyData !== 'Common' &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.bodyHeader : 'Body'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_bodyData}</TableCell>
                        </TableRow>}
                        {var_regionData !== 'Common' &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.regionHeader : 'Region'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_regionData}</TableCell>
                        </TableRow>}
                        {var_vinFromData !== 'XXXXXXXXXXXXXXXXX' &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.vinFromHeader : 'VIN From'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_vinFromData}</TableCell>
                        </TableRow>}
                        {var_vinToData !== 'XXXXXXXXXXXXXXXXX' &&
                        <TableRow>
                            <TableCell className={thRowClass} component="th" align="center">{window.isTCPublish ? window.vinToHeader : 'VIN To'}</TableCell>
                            <TableCell colSpan={2} className={tdRowClass}
                                       align="center">{var_vinToData}</TableCell>
                        </TableRow>}
                        {var_revisionCreated &&
                        <TableRow>
                            <TableCell className={`dpl-th-vertical revision-header ${directionClass}`} component="th"
                                       align="center">{window.isTCPublish ? window.revisionsHeader : 'Revisions'}</TableCell>
                            <TableCell className={`dpl-td-vertical revision-row-title ${directionClass}`}
                                       align="center">{window.isTCPublish ? window.createdHeader : 'Created'}</TableCell>
                            <TableCell className={`revision-data ${tdRowClass}`}
                                       align="center">{var_revisionCreated}</TableCell>
                        </TableRow>}
                        {var_revisionDateData &&
                        <TableRow>
                            <TableCell className="hide-header" style={{border: 'none'}} align="center"></TableCell>
                            <TableCell className={`dpl-td-vertical revision-row-title ${directionClass}`}
                                       align="center">{window.isTCPublish ? window.revisedHeader : 'Revised'}</TableCell>
                            <TableCell className={`revision-data ${tdRowClass}`}
                                       align="center">{var_revisionDateData}</TableCell>
                        </TableRow>}
                        {var_revision &&
                        <TableRow>
                            <TableCell className="hide-header" style={{border: 'none'}} align="center"></TableCell>
                            <TableCell className={`dpl-td-vertical revision-row-title ${directionClass}`}
                                       align="center">{window.isTCPublish ? window.commentsHeader : 'Comments'}</TableCell>
                            <TableCell className={`revision-data ${tdRowClass}`}
                                       align="center">{var_revision}</TableCell>
                        </TableRow>
                        }
                    </TableBody>
                </Table>
            </TableContainer>
        ));
    }

    useEffect(() => {
        changeHeight();
    }, [])

    const classes = useStyles();
    return (
        <div id="manualContent" className={classes.tab}>
            <AppBar className="tab-header" position="static" color="default" style={{boxShadow: 'none'}}>
                <GlobalCss/>
                <Tabs value={value} onChange={handleChange} indicatorColor="white" textColor="white" variant="fullWidth"
                      aria-label="full width tabs example">
                    <Tab className={`tab-title ${classes.indtab}`}
                         style={{borderLeft: 'solid 2.5px white', borderRight: 'solid 2.5px white'}}
                         label={window.isTCPublish ? window.documentTab : 'Document'} {...a11yProps(0)} />
                    <Tab className={`tab-title ${classes.indtab}`}
                         style={{borderLeft: 'solid 2.5px white', borderRight: 'solid 2.5px white'}}
                         label={window.isTCPublish ? window.procedureTab : 'Procedure'} {...a11yProps(1)} />
                    <Tab className={`tab-title ${classes.indtab}`}
                         style={{borderLeft: 'solid 2.5px white', borderRight: 'solid 2.5px white'}}
                         label={window.isTCPublish ? window.partsTab : 'Parts'} {...a11yProps(2)} />
                    <Tab className={`tab-title ${classes.indtab}`}
                         style={{borderLeft: 'solid 2.5px white', borderRight: 'solid 2.5px white'}}
                         label={window.isTCPublish ? window.detailsTab : 'Details'}  {...a11yProps(3)} />
                    <Tab className={`tab-title ${classes.indtab}`}
                         style={{borderLeft: 'solid 2.5px white', borderRight: 'solid 2.5px white'}}
                         label={window.isTCPublish ? window.includesTab : 'Includes'}  {...a11yProps(4)} />
                    <Tab className={`tab-title ${classes.indtab}`}
                         style={{borderLeft: 'solid 2.5px white', borderRight: 'solid 2.5px white'}}
                         label={window.isTCPublish ? window.suppliesTab : 'Supplies'}  {...a11yProps(5)} />
                    <Tab className={`tab-title ${classes.indtab}`}
                         style={{borderLeft: 'solid 2.5px white', borderRight: 'solid 2.5px white'}}
                         label={window.isTCPublish ? window.sstTab : 'SST'}  {...a11yProps(6)} />
                </Tabs>
            </AppBar>

            <Typography
                component="div"
                role="tabpanel"
                hidden={value !== 0}
                id="tab-content-0"
                className={"tab-content"}
                onClick={handleTabContentClick}
            >
                {/*<div id="TabDoc"></div>*/}
                <div className={classes.root} id="TabDoc">
                    <Accordion id="document-details" className="panel-acc ">
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon className="expand-arrow-icon"/>}
                            aria-controls="panel1a-content"
                            id="panel1a-header"
                        >
                            <Typography className={`tab-title ${classes.heading}`}>{window.isTCPublish ? window.detailsTab : 'Details'} </Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <div id="AccordianTaskDetailsContent"></div>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion id="document-parts" className="panel-acc panel-spacing">
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon className="expand-arrow-icon"/>}
                            aria-controls="panel2a-content"
                            id="panel2a-header"
                        >
                            <Typography className={`tab-title ${classes.heading}`}>{window.isTCPublish ? window.partsTab : 'Parts'}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <div id="AccordianTaskPartsContent"></div>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion id="document-supplies" className="panel-acc panel-spacing">
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon className="expand-arrow-icon"/>}
                            aria-controls="panel3a-content"
                            id="panel3a-header"
                        >
                            <Typography className={`tab-title ${classes.heading}`}>{window.isTCPublish ? window.suppliesTab : 'Supplies'} </Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <div id="AccordianTaskSupplyContent"></div>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion id="document-sst" className="panel-acc panel-spacing">
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon className="expand-arrow-icon"/>}
                            aria-controls="panel4a-content"
                            id="panel4a-header"
                        >
                            <Typography className={`tab-title ${classes.heading}`}>{window.isTCPublish ? window.sstTab : 'SST'}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <div id="AccordianTaskEquipmentContent"></div>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion id="document-procedure" className="panel-acc panel-spacing">
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon className="expand-arrow-icon"/>}
                            aria-controls="panel5a-content"
                            id="panel5a-header"
                        >
                            <Typography className={`tab-title ${classes.heading}`}>{window.isTCPublish ? window.procedureTab : 'Procedure'}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <div id="AccordianTaskContent"></div>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion id="document-include" className="panel-acc panel-spacing">
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon className="expand-arrow-icon"/>}
                            aria-controls="panel5a-content"
                            id="panel5a-header"
                        >
                            <Typography className={`tab-title ${classes.heading}`}>{window.isTCPublish ? window.includesTab : 'Includes'}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <div id="AccordianTaskIncludeContent" style={{ width: '100%' }}>{ detailTable}</div>
                        </AccordionDetails>
                    </Accordion>
                </div>
            </Typography>
            <Typography
                component="div"
                role="tabpanel"
                hidden={value !== 1}
                id="tab-content-1"
                className={"tab-content"}
            >
                <div id="filterProcedure" className="filterTitle">{window.isTCPublish ? window.labelShow : 'Show'}:
                    {/*<span className="filterLabel">All</span>*/}
                    <button id="buttonAll" className="filterLabel" style={{cursor: "grabbing"}}><label>{window.isTCPublish ? window.labelAll : 'All'}</label>
                    </button>
                    <span>|</span>
                    {/*<span className="filterLabel">Torque Figures</span>*/}
                    <button id="buttonTorque" className="filterLabel" style={{cursor: "grabbing"}}><label>{window.isTCPublish ? window.labelTorque : 'Torque Figures'}</label></button>
                </div>
                <div id="TabProcedure"></div>
            </Typography>
            <Typography
                component="div"
                role="tabpanel"
                hidden={value !== 2}
                id="tab-content-2"
                className={"tab-content"}
                onClick={handleTabContentClick}
            >
                <div className="labelCurrent">{window.isTCPublish ? window.currentProcedure : 'Current Procedure'}</div>
                <div id="TabParts"></div>
                {
                    window.isTCPublish &&
                    <div>
                        <div className="labelIncluded">{window.isTCPublish ? window.includedProcedure : 'Included Procedures'}</div>
                        <div id="IncludedTabParts">
                        </div>
                    </div>
                }
            </Typography>

            <Typography
                component="div"
                role="tabpanel"
                hidden={value !== 3}
                id="tab-content-3"
                className={"tab-content"}
            >
                <div>
                    {
                        detailTab
                    }
                </div>
            </Typography>

            <Typography
                component="div"
                role="tabpanel"
                hidden={value !== 4}
                id="tab-content-4"
                className={"tab-content"}
            >{
                window.isTCPublish && <div>
                    <div id="filterIncludes" className="filterTitle">
                        <button id="buttonExpandList" className="filterLabel" style={{cursor: "pointer"}}>{window.isTCPublish ? window.labelExpandList : 'Expand List'}
                        </button>
                        <span>|</span>
                        <button id="buttonCollapseList" className="filterLabel" style={{cursor: "pointer"}}>{window.isTCPublish ? window.labelCollapseList : 'Collapse List'}
                        </button>
                    </div>
                    <div id="TabIncludes">
                        {
                            detailTable
                        }
                    </div>
                </div>
            }
                {/*{window.isTCPublish || <text>There are no Includes for this page.</text>}*/}
            </Typography>

            <Typography
                component="div"
                role="tabpanel"
                hidden={value !== 5}
                id="tab-content-5"
                className={"tab-content"}
                onClick={handleTabContentClick}
            >
                <div className="labelCurrent">{window.isTCPublish ? window.currentProcedure : 'Current Procedure'}</div>
                <div id="TabSupplies"></div>
                {
                    window.isTCPublish &&
                    <div>
                        <div id="includedSuppliesLabel" className="labelIncluded">{window.isTCPublish ? window.includedProcedure : 'Included Procedures'}</div>
                        <div id="IncludedTabSupplies"></div>
                    </div>
                }
            </Typography>

            <Typography
                component="div"
                role="tabpanel"
                hidden={value !== 6}
                id="tab-content-6"
                className={"tab-content"}
                onClick={handleTabContentClick}
            >
                <div className="labelCurrent">{window.isTCPublish ? window.currentProcedure : 'Current Procedure'}</div>
                <div id="TabSST"></div>
                {
                    window.isTCPublish &&
                    <div>
                        <div className="labelIncluded">{window.isTCPublish ? window.includedProcedure : 'Included Procedures'}</div>
                        <div id="IncludedTabSST">
                        </div>
                    </div>
                }
            </Typography>

        </div>

    );
}
