import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import Divider from '@material-ui/core/Divider';
import Box from '@material-ui/core/Box';
import Logo from '../Assets/Ineos-Automotive-logo.png';

const useStyles = makeStyles((theme) => ({
    HeaderDiv: {
        display: "flex",
        flexDirection: "column"
    },
    formControl: {
        // margin: theme.spacing(1),
        minWidth: 100,
    },
    root: {
        '& hr': {
            margin: theme.spacing(0, 0.5),
        },
    },
    Divider: {
        backgroundColor: "white",
        margin: "5px 10px"
    },
    Show: {
        zIndex: 5000
    }
}));

const Header = () => {
    const classes = useStyles();

    const HeaderElement = (title, label, id) => {
        return (
            <div
                className={classes.HeaderDiv}
            >
                <p className={"HeaderTitleText"}>
                    {title}
                </p>
                <p className={"HeaderLabelText"} id={id}>
                    {label}
                </p>
            </div>
        );
    }
    const onlyHeaderElement = (title, id) => {
        return (
            <div
                className={classes.HeaderDiv}
            >
                <p className={"HeaderTitleText"}>
                    {title}
                </p>
            </div>
        );
    }
    const nonHeaderElement = (label, id) => {
        return (
            <div
                className={classes.HeaderDiv}
            >
                <p className={"HeaderLabelLargeText"} id={id}>
                    {label}
                </p>
            </div>
        );
    }

    const [TabValue, setTabValue] = useState('Prrequis');

    window.setHeaderTabValue = function setHeaderTabValue(value) {
        setTabValue(value);
    }

    return (
        // eslint-disable-next-line no-undef
        <React.Fragment>
            <CssBaseline/>
            <AppBar position="fixed">
                <Toolbar className="AppBar">
                    <Box className="segment segment_Img" width={"400px"}>
                        <Box>
                            <img src={Logo} alt="logoCat" id="logo_brand"/>
                        </Box>
                    </Box>
                    <Divider orientation="vertical" className={classes.Divider} flexItem/>
                    <Box className="segment segment_Text_5" width={"0.25"}>
                        {nonHeaderElement(window.docTitle,"doc_title")}
                    </Box>
                    <Divider orientation="vertical" className={classes.Divider} flexItem/>
                    <Box  className="segment segment_Text_1"  width={"0.25"}>
                        {HeaderElement("Job Code", window.jobCode ,"job_code")}
                    </Box>
                    <Divider orientation="vertical" className={classes.Divider} flexItem/>
                    <Box className="segment segment_Text_2" width={"0.15"}>
                        {HeaderElement("Work Station Code",window.station,"workstation_code")}
                    </Box>
                    <Divider orientation="vertical" className={classes.Divider} flexItem/>
                    <Box className="segment segment_Text_3" width={"0.2"}>
                        {HeaderElement("Model Description",window.modelDescription,"model_desc")}
                    </Box>
                    <Divider orientation="vertical" className={classes.Divider} flexItem/>
                    <Box className="segment segment_Text_4" width={"0.1"}>
                        {HeaderElement("Author",window.author,"author")}
                    </Box>
                    <Divider orientation="vertical" className={classes.Divider} flexItem/>
                    <Box className="segment segment_Text_5" width={"0.1"}>
                        {HeaderElement("Revision",window.revision,"revision")}
                    </Box>
                    <Divider orientation="vertical" className={classes.Divider} flexItem/>
                    <Box className="segment segment_Text_5" width={"0.15"}>
                        {HeaderElement("Released Date",window.releasedDate,"release_date")}
                    </Box>
                    {/*<Box className="segment segment_Title" width={"200px"}>*/}
                    {/*    {onlyHeaderElement(TabValue,"tabValue")}*/}
                    {/*</Box>*/}
                </Toolbar>
            </AppBar>
        </React.Fragment>
    );
};

export default Header;